* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.main{
  display: flex;
  justify-content: center;
}
.App{
  width: 50%;
  text-align: center;
  border: 1px solid lightgray;
  padding: 30px 45px;
  border-radius: 8px;
  margin-top: 30px;
}
.header{
  background-color: lightgray;
  padding: 10px;
  color: #CE93D8;
}
.balance{
  text-align: center;
  background-color: white;
  padding: 10px 0px;
}
.balance p{
  font-size: 20px;
  font-family: 'poppins', sans-serif;
  margin: 10px 0px 0px 0px;
  color: palegreen;
}
.balance h3{
  font-family: 'poppins', sans-serif;
  font-size: 30px;
  font-weight: 300;
  margin: auto;
  margin-top: 10px;
  background-color: whitesmoke;
  width: 200px;
  padding: 10px;
  border-radius: 8px;
}
.plus{
  color: lightgreen;
}
.minus{
  color: red;
}
.inc-exp-container{
  display: flex;
  justify-content: space-evenly;
  margin-top: 20px;
}
.inc-exp-container p{
  font-size: 20px;
  margin-bottom: 4px;
}
.inc-box{
  background-color: green;
  padding: 8px 20px;
  color: white;
  border-radius: 5px;
}
.exp-box{
  background-color: red;
  padding: 8px 20px;
  color: white;
  border-radius: 5px;
}
.Transaction-list{
  margin: 25px auto;
  text-align: left;
  width: 80%;
}
.Transaction-list h3{
  padding-bottom: 10px;
  margin-bottom: 15px;
  border-bottom: 1px solid lightgreen;
}
.exp{
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  background-color: whitesmoke;
  margin: 10px 0px;
  border-left: 2px solid red;
  padding: 6px 10px;
}
.monie-plus{
  background-color: green;
  padding: 3px 8px;
  color: white;
  border-radius: 3px;
}
.monie-minus{
  background-color: red;
  padding: 3px 8px;
  color: white;
  border-radius: 3px;
}
.inc{
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  background-color: whitesmoke;
  margin: 10px 0px;
  padding: 6px 10px;
  border-right: 2px solid green;
}
.exp span,
.inc span{
  font-family: 'poppins';
}
.list{
  margin: 10px 0px;
  height: 200px;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.list::-webkit-scrollbar{
  width: 2px;
}
.form-control{
  width: 100%;
}
.form-control input{
  width: 100%;
  padding: 10px;
  margin: 10px;
  outline: none;
  border: 1px solid #CE93D8;
  border-radius: 5px;
}
.btn{
  padding: 8px;
  background-color: green;
  color: whitesmoke;
  outline: none;
  border: none;
}

@media screen and (max-width: 540px){
  .App{
    width: 95%;
  }
  .Transaction-list{
    width: 100%;
  }
  .inc-box,
  .exp-box{
    width: 220px;
    margin: 0px 10px;
  }
}